import { Link } from 'react-router-dom';
import './NewsList.scss';
import NewsItem from './NewsItem';
import { ButtonLink } from '../../atoms';

const NewsList = () => {
  const listNews = [
    {
      title: ' VPN         WireGuard',
      url: 'news1',
      img: 'news/image2.png',
      type: ['']
    },
    {
      title: '  Pixel 7  7 Pro     Google One VPN',
      url: 'news2',
      img: 'news/image2.png',
      type: ['']
    },
    {
      title: ' Google Play     VPN-',
      url: 'news3',
      img: 'news/image2.png',
      type: ['']
    },
    {
      title:
        ' VPN-  iOS      Apple    ,    ',
      url: 'news4',
      img: 'news/image2.png',
      type: ['']
    },
    {
      title: ' Tailscale VPN    ',
      url: 'news5',
      img: 'news/image2.png',
      type: ['', '']
    }
  ];
  return (
    <div className="news-list__inner">
      <div className="news__list">
        {listNews.map((node, key) => {
          return <NewsItem key={key} item={node} />;
        })}
      </div>
      <div className="btn btn_all_news">
        <ButtonLink text=" " url="/news" align="center" />
      </div>
    </div>
  );
};

export default NewsList;
